___CSS_LOADER_EXPORT___.push([module.id, `.task-form-container {
    display: none;
    flex-direction: column;
    padding: 10px;
    border-radius: 0.6rem;
    border: 1px solid #9496a0;
    width: 100%;
}

#task-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.field-container {
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
    width: 100%;
}

.task-field {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    border: none;
    padding: 4px;
}

.task-field:focus-visible {
    outline: none;
}

.task-btn-container {
    display: flex;
    gap: 1rem;
}

.task-btn-wrapper {
    display: flex;
    padding: 0.3em .5em;
    border: 1px solid #DBDFEA;
    border-radius: 0.3em;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.task-btn-wrapper:hover {
    background-color: #f2f2f3;
}

.btn-icon {
    width: 1rem;
}

.task-btn {
    cursor: pointer;
    background: none;
    border: none;
}

.control-btn-container {
    position: relative;
    display: flex;
    gap: 1rem;
    justify-content: flex-end;
    border-top: 1px solid #9496a0;
    padding: 0.7rem 0 0 0;
}

.control-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.7em 0.9em;
    border-radius: 0.3rem;
    border: none;
    color: #3f3f3f;
    font-weight: 900;
}

.control-btn:hover {
    background-color: #DBDFEA;
}

#form-add-task-btn {
    background-color: #ACB1D6;
    color: #fff;
}

#form-add-task-btn:hover {
    background-color: #8294C4;
}

#date-btn-container {
    position: relative;
}

.date-form-container {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: -40px;
    left: 1px;
    gap: 5px;
    z-index: 10;
    padding: 5px;
    border-radius: 4px;
    background-color: #fff;
    box-shadow: 0 0 4px 2px rgba(64, 64, 64, 0.1);
}

.date-fields {
    display: flex;
    border: none;
    width: fit-content;
    background: none;
    justify-content: center;
    align-items: center;
}


.date-displayer-container {
    display: flex;
    gap: 5px;
    width: 100%;
    justify-content: center;
}

#date-confirm-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    background: none;
    border: none;
    border-radius: 5px;
    width: 20px;
    height: 20px;
    cursor: pointer;
}

#date-confirm-btn:hover {
    background-color: #DBDFEA;
}

#date-confirm-btn-icon {
    rotate: -90deg;
    width: 20px;
}

/* Remove up arrow and down arrow button*/
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

#yy:focus-visible,
#mm:focus-visible,
#dd:focus-visible {
    border: none;
    outline: none;
}`, "",{"version":3,"sources":["webpack://./src/css/task-form.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,qBAAqB;IACrB,yBAAyB;IACzB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,WAAW;AACf;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,2BAA2B;IAC3B,WAAW;IACX,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,yBAAyB;IACzB,oBAAoB;IACpB,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,SAAS;IACT,yBAAyB;IACzB,6BAA6B;IAC7B,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,oBAAoB;IACpB,qBAAqB;IACrB,YAAY;IACZ,cAAc;IACd,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;IACzB,WAAW;AACf;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,UAAU;IACV,SAAS;IACT,QAAQ;IACR,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,sBAAsB;IACtB,6CAA6C;AACjD;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,kBAAkB;IAClB,gBAAgB;IAChB,uBAAuB;IACvB,mBAAmB;AACvB;;;AAGA;IACI,aAAa;IACb,QAAQ;IACR,WAAW;IACX,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,gBAAgB;IAChB,YAAY;IACZ,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,cAAc;IACd,WAAW;AACf;;AAEA,yCAAyC;AACzC;;IAEI,wBAAwB;IACxB,SAAS;AACb;;AAEA;;;IAGI,YAAY;IACZ,aAAa;AACjB","sourcesContent":[".task-form-container {\n    display: none;\n    flex-direction: column;\n    padding: 10px;\n    border-radius: 0.6rem;\n    border: 1px solid #9496a0;\n    width: 100%;\n}\n\n#task-form {\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n}\n\n.field-container {\n    display: flex;\n    flex-direction: column;\n    gap: 0.2rem;\n    width: 100%;\n}\n\n.task-field {\n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n    width: 100%;\n    border: none;\n    padding: 4px;\n}\n\n.task-field:focus-visible {\n    ou